.main-title {
    /* font-family: 'Amaranth', sans-serif; */
    font-family: var(--title-font);
    font-size: 4.8em;
}

.home-screen {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url('https://source.unsplash.com/Wmuu76rFl0Y/700x500');
    /* background-repeat: no-repeat; */
    background-size: cover;
    /* background-position: top; */
    /* background-image: url('https://source.unsplash.com/2M_sDJ_agvs/1000x800'); */
}

.search-container {
    display: flex;
    align-items: center;
}

.search-box-container {
    margin: 1.5em;
}

.home-search-box {
    height: 2.5em;
}

.search-button {
    padding: 0.5em 0.7em;
    /* margin: 1em; */
    border-radius: 0.5em;
    font-size: 1.4em;
    background-color: var(--dark-button-color);
    border: none;
    cursor: pointer;
}

.buttons-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-button {
    padding: 1.5em 1.2em;
    margin: 1em;
    border-radius: 0.5em;
    background-color: var(--light-button-color);
    font-size: 1.4em;
    font-weight: bold;
    border: none;
    cursor: pointer;
}

.nav-button:hover {
    background-color: var(--dark-button-color);
    color: white;
}

@media(min-width: 768px) {
    .main-title {
        font-size: 6em;
    }

    .search-container {
        margin-bottom: 1em;
    }

    .home-search-box {
        height: 3em;
    }

    .search-button {
        font-size: 1.6em;
        padding: 0.7em 1em;
    }

    .nav-button {
        padding: 1.5em;
        font-size: 1.5em;
    }
}

@media(min-width: 1200px) {

}
.map-container {
    width: 85%;
    height: 300px;
    margin: auto;
}

.search-input-container {
    display: flex;
    justify-content: space-evenly;
    padding: 1rem;
}

.search-box {
    box-sizing: border-box;
    width: 20rem;
    border: 1px solid transparent;
    border-radius: 3px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
    font-size: 12px;
    outline: none;
    text-overflow: ellipses;
    padding: 2px 10px;
}

.info-window-container {
    display: flex;
    align-items: center;
    margin: .5em;
}
.info-window-image {
    width: 50px;
    height: 50px;
}

.info-window-section {
    margin: .5em;
}

.info-window-title {
    font-family: var(--main-text-font);
    font-weight: bold;
    margin: 0;
    font-size: 1.2rem;
}

.info-window-status {
    font-family: var(--main-text-font);
    font-size: 1rem;
}

.info-window-link {
    font-family: var(--main-text-font);
    font-size: 1.2rem;
    margin: 0;
}

@media(min-width: 360px) {
    .info-window-image {
        width: 70px;
        height: 70px;
    }
    .info-window-title, .info-window-link {
        font-size: 1.5rem;
    }
    .info-window-status {
        font-size: 1.2rem;
    }
}

@media(min-width: 768px) {
    .search-box {
        width: 33rem;
        font-size: 1.4rem;
    }
}

@media(min-width: 1200px) {
    .search-box {
        width: 45rem;
    }
}
.form-screen {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.success-image-background {
    background-image: url('https://source.unsplash.com/LLcAJbEh1_Q/800x600');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: bottom;
}

.form-title {
    margin: .5em 0 0 0;
    font-size: 3rem;
    font-family: 'Cabin Sketch', cursive;
    text-align: center;
}

.add-item-form {
    width: 300px;
    height: -webkit-max-content;
    height: -moz-max-content;
    height: max-content;
    margin: auto;
    align-items: center;
    border: 2px solid var(--dark-button-color);
    background-color: var(--light-button-color);
    border-radius: 1em;
    overflow-y: scroll;
}

.form-section-title {
    font-family: var(--title-font);
    font-weight: bold;
    font-size: 1.5rem;
    text-align: center;
    margin: .5em;
}

.form-text { 
    font-family: var(--main-text-font);
    font-size: 1.2rem;
    text-align: center;
    margin: .5em;
    letter-spacing: .05rem;
    line-height: 1.5;
}

.id-box {
    height: 4rem;
}

.id-text {
    margin: 0;
    font-size: 2rem;
    text-align: center;
    padding: .5rem;
}
.form-submit-button {
    background-color: var(--dark-button-color);
    font-size: 1.7rem;
    padding: .5em;
    width: 10rem;
    border-radius: .3em;
    border: none;
    color: white;
    font-weight: bold;
    margin: .8em;
}

.form-button {
    background-color: var(--dark-button-color);
    font-size: 1.2rem;
    padding: .5em 1em;
    border-radius: .3em;
    border: none;
    color: white;
    font-weight: bold;
    margin: .5em;
    text-decoration: none;
}

.input-box {
    background-color: white;
    box-sizing: border-box;
    border: 1 px solid transparent;
    width: 200px;
    border: 1px solid transparent;
    border-radius: 3px;
    box-shadow: 0 2px 6px rgba(255, 149, 11, 0.3);
    font-size: 12px;
    outline: none;
    /* text-overflow: ellipses; */
    padding: 2px 10px;

}

.form-section {
    display: flex;
    flex-direction: column;
    width: 75%;
    align-items: center;
    margin: 1em auto
}

.description-box {
    height: 70px;
}

.form-btns-container {
    display: flex;
}

@media(min-width: 768px) {
    .add-item-form {
        width: 500px;
    }
    .form-title {
        font-size: 4rem;
    }
    .form-section-title {
        font-size: 2.5rem;
    }
    .input-box {
        font-size: 2rem;
        width: 90%;
    }
    .form-button {
        font-size: 2rem;
    }
    .description-box {
        height: 100px;
    }
    .form-text {
        font-size: 1.5rem;
    }
}
.found-items-image-background {
    background-image: url('https://source.unsplash.com/2M_sDJ_agvs/1400x1600');
    /* background-repeat: no-repeat; */
    background-size: cover;
    background-position: center;
}

.search-items-button {
    padding: 0.5em 0.7em;
    border-radius: 0.5em;
    font-size: 1.4em;
    background-color: var(--dark-button-color);
    border: none;
    cursor: pointer;
}

.search-id-form-section {
    display: flex;
}
